import React, { useEffect, useState } from 'react';
import {Form} from 'react-formio';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { CButton, CCard, CCardBody, CCardFooter, CCardHeader, CCol, CCollapse, CRow } from '@coreui/react';

export default function DisplayForm(props) {

    const [form, setForm] = useState({});
    const [collapse, setCollapse] = useState(true);
    const [submission, setSubmission] = useState({});
    const { match: { params } } = props;
    const login = useSelector(state => state.login)
    const form_id =  params.id;
    useEffect(()=>{
        const url = login.isAdmin?`/form/admin/${form_id}`
              :`/form/${form_id}`
        axios.get(url).then(res=>{
            let _form =  res.data.form.form;
            // _form.components[0].defaultValue="asd" to set data in the form
            // _form.components[2].defaultValue="15:10:10"
            setForm(_form)
            // console.log(form.title)
            // console.log(form)
            // console.log(res.data.form.form)
        }).catch(err=>{
            alert("Not Found")
            props.history.push('/my-surveys')
        })
    },[])
    
    const handleSubmit = (data)=> {
        const user_form_data = {data:{}};
       
        for(let user_data in data.data){ 
            form.components.map(item =>{
                if(item.key === user_data){
                   
                    user_form_data['data'][user_data] = {
                        id:item.id,
                        key:item.key,
                        value:data.data[user_data],
                        user:"user",
                        label:item.label,
                        input_type:item.type
                    }
                    return true;
                }
            })
        }
        // data.data = user_form_data;
        user_form_data["form_id"] = form_id;
        user_form_data["form_title"] = form.title;
        console.log(user_form_data)
        axios.post("/form/data",user_form_data).then(res=>{
            console.log("her i submit data ")
            setSubmission({data})
            console.log(res)
            props.history.push("/my-surveys")
        }).catch(err=>{
            alert("something Wrong")
        })
        // // console.log(data)
        // props.history.push("/form/list")
      }

  return (
        <div>
            <h1>Report</h1>
            {form?.description?.show && <CRow  >
                <CCol xl="12">
                <CCard>
                <CCardHeader>
                    Report Description
                </CCardHeader>
                <CCollapse show={collapse}>
                    <CCardBody>
                    
                    <p>
                        {form.description.value}
                    </p>

                    </CCardBody>
                </CCollapse>
                <CCardFooter>
                    <CButton
                    color="primary"
                    onClick={()=>setCollapse(!collapse)}
                    className={'mb-1'}
                    >Show Description</CButton>
                </CCardFooter>
                </CCard>
                </CCol>
            </CRow>}
            <hr/>
            <Form 
                form={form} 
                submission={{ data: submission }}
                onSubmit={handleSubmit}  
                onSubmitDone={a => {
                    console.log(a);
                  }}
                options={{ template: 'bootstrap3', readOnly:false }}
                />
            
        </div>
    );
  
}